
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'


import Uploader from 'assembly/uploader/uploader'

export default {
data () {
return {
switchIndex: 0,
photoTree: [
{label: '专卖店正面照片', value: 'photo'},
{label: '专卖店内部照片', value: 'photo2'},
{label: '专卖店左侧照片', value: 'photo3'},
{label: '专卖店右侧照片', value: 'photo4'}
],
photoTree2: [
{label: '合同封面照片', value: 'pFrontPhoto'},
{label: '合同首页照片', value: 'pCoverPhoto'},
{label: '合同签名页(含甲乙双方)', value: 'pSignPhoto'}
],
userInfo: {},
fwi: {
ownerSex: '', // 店主性别
shopkeeperage: '', // 店主年龄
saleMan: '', // 业务员
salesCount: '', // 人员配置
setupEmp: '', // 开店员工
empDegree: '', // 员工身份
hasStationEmp: '', // 是否有驻店员工
bopExplain: '', // BOP讲解
outletArea: '', // 面积
rentCost: '', // 租金
clerkmonthlysalary: '', // 店员月工资
monthSalesAvg: '', // 平均月销售
profitAndLoss: '', // 月盈亏平衡
addressSource: '', // 地址来源
msgSource: '', // 信息来源
location: '', // 具体位置
outletShow: '', // 门店外观
visualizeGrade: '', // 店面形象
photo: '', // 专卖店图片(正)
photo2: '', // 专卖店图片2(内)
photo3: '', // 专卖店图片3(左)
photo4: '' 
},
contract: {
pDbProtocolNo: '', // 合同编号
pOrganName: '', // 合同客户名称
pCardNo1: '', // 身份证号码
pDbProtocolAddr: '', // 合同联系地址
pDbProtocolDateE: '', // 合同有效期结束日期
pDbProtocolDateS: '', // 合同有效期开始日期
pCoverPhoto: '', // 合同首页照片
pSignPhoto: '' 
},
citparent: {
pUid: '',
pName: '',
pAddress1: ''
},
dfs: {
pName: '',
pMobile: ''
},
dm: {
pName: '',
pMobile: ''
}
}
},
methods: {
goBack () {
this.$util.getMachine(4)
},
switchTabItem (index) {
this.switchIndex = index
},
toCompleteApply () {
this.$router.push({
path: '/completeApply'
})
},
beforeSend (file) {
return false



},
imgClick (indexs, index) {
this.ImgNum = index
this.arrNum = indexs
},
onFinish (res) {
this.declarationProductVOS[this.arrNum].photos.splice(this.ImgNum, 1, res.model)
},
_getInfo () {
this.$tupHttp(this, 'front/user/info/v1').then(res => {
this.userInfo = res.data.model
this.userInfo.pOpenningDate = this.userInfo.pOpenningDate ? new Date(this.userInfo.pOpenningDate).format('yyyy-MM-dd') : this.userInfo.pOpenningDate
this.userInfo.pFirstorderDate = this.userInfo.pFirstorderDate ? new Date(this.userInfo.pFirstorderDate).format('yyyy-MM-dd') : this.userInfo.pFirstorderDate
this.userInfo.pGraduationDate = this.userInfo.pGraduationDate ? new Date(this.userInfo.pGraduationDate).format('yyyy-MM-dd') : this.userInfo.pGraduationDate
this.userInfo.fwi = this.userInfo.fwi ? this.userInfo.fwi : this.fwi
this.userInfo.contract = this.userInfo.contract ? this.userInfo.contract : this.contract
this.userInfo.contract.pDbProtocolDateS = this.userInfo.contract.pDbProtocolDateS ? new Date(this.userInfo.contract.pDbProtocolDateS).format('yyyy-MM-dd') : this.userInfo.contract.pDbProtocolDateS
this.userInfo.contract.pDbProtocolDateE = this.userInfo.contract.pDbProtocolDateE ? new Date(this.userInfo.contract.pDbProtocolDateE).format('yyyy-MM-dd') : this.userInfo.contract.pDbProtocolDateE
this.userInfo.citparent = this.userInfo.citparent ? this.userInfo.citparent : this.citparent
this.userInfo.dfs = this.userInfo.dfs ? this.userInfo.dfs : this.dfs
this.userInfo.dm = this.userInfo.dm ? this.userInfo.dm : this.dm
})
}
},
created () {
this._getInfo()
},
components: {
XHeader,
Tab,
TabItem,
Group,
Cell,
XInput,
Uploader
}
}



